<template>
  <div
    class="d2-layout-header-aside-group"
    :style="styleLayoutMainGroup"
    :class="{grayMode: grayActive}">
    <!-- 半透明遮罩 -->
    <div class="d2-layout-header-aside-mask"></div>
    <!-- 主体内容 -->
    <div  class="d2-layout-header-aside-content" flex="dir:top">
      <!-- 顶栏 -->
       
     



      <!-- 下面 主体 -->
      <div class="d2-theme-container" flex-box="1" flex>
        <!-- 主体 侧边栏 -->
        <div
          flex-box="0"
          ref="aside"
          class="d2-theme-container-aside"
           
          :style="{
            width: asideCollapse ? asideWidthCollapse : asideWidth,
            opacity: this.searchActive ? 0.5 : 1
          }">
           <div class="logo-group" style="display: flex;" :style="{width: asideCollapse ? asideWidthCollapse : asideWidth}" flex-box="0">
            <img  v-if="!asideCollapse"
              style="width:86.875px;height: 25px;margin-top: 18px;margin-left: 30px;"
              src="./image/matou_logo.png"
              alt="Fork me on GitHub">
            <img  v-if="asideCollapse"
              style="width:35.9px;height: 30px;margin-top: 15px;"
              src="./image/logos.png"
              alt="Fork me on GitHub">
          </div>
          <d2-menu-side style="margin-top:60px;"/>
        </div>
        <!-- 主体 -->
        <div class="d2-theme-container-main" flex-box="1" flex>
          <!-- 内容 -->
          
          <transition name="fade-scale">
            <div v-if="!searchActive" class="d2-theme-container-main-layer" flex="dir:top">
              <!-- tab -->
              <div style="display: none;" class="d2-theme-container-main-header" flex-box="0">
                <d2-tabs />
              </div>
              <!-- 页面 -->
              <div class="d2-theme-container-main-body" flex-box="1">
                
                <transition :name="transitionActive ? 'fade-transverse' : ''">
                  <keep-alive :include="keepAlive">
                    <router-view/>
                  </keep-alive>
                </transition>
                 <div class="d2-header-right" flex-box="0">
                   <d2-header-user/>
                </div>
              </div>
            </div>
            
            
          </transition>
            <!-- <div class="d2-header-right" flex-box="0">
             <d2-header-user/>
            </div> -->
        </div>
        



      </div>
    </div>
  </div>
</template>

<script>
import d2MenuSide from './components/menu-side'
import d2MenuHeader from './components/menu-header'
import d2Tabs from './components/tabs'
import d2HeaderSearch from './components/header-search'
import d2HeaderUser from './components/header-user'
import { mapState, mapGetters, mapActions } from 'vuex'
import mixinSearch from './mixins/search'
import menuHeader from '@/menu/header'
import menuAside from '@/menu/aside'
import d2HeaderTheme from './components/header-theme'

export default {
  name: 'd2-layout-header-aside',
  mixins: [
    mixinSearch
  ],
  components: {
    d2MenuSide,
    d2MenuHeader,
    d2Tabs,
    d2HeaderSearch,
    d2HeaderUser,
    d2HeaderTheme
  },
  data () {
    return {
      // [侧边栏宽度] 正常状态
      asideWidth: '160px',
      // [侧边栏宽度] 折叠状态
      asideWidthCollapse: '40px',
      pageName:"",
    }
  },
  computed: {
    ...mapState('d2admin', {
      keepAlive: state => state.page.keepAlive,
      grayActive: state => state.gray.active,
      transitionActive: state => state.transition.active,
      asideCollapse: state => state.menu.asideCollapse
    }),
    ...mapGetters('d2admin', {
      themeActiveSetting: 'theme/activeSetting'
    }),
    /**
     * @description 最外层容器的背景图片样式
     */
    styleLayoutMainGroup () {
      return {
        ...this.themeActiveSetting.backgroundImage ? {
          backgroundImage: `url('${this.$baseUrl}${this.themeActiveSetting.backgroundImage}')`
        } : {}
      }
    }
  },
  created(){
    // 设置顶栏菜单
    this.$store.commit('d2admin/menu/headerSet', menuHeader())
    // 设置侧边栏菜单
    this.$store.commit('d2admin/menu/asideSet', menuAside())
    this.pageName = this.$route.name
    console.log('layoutName:',this.pageName)
  },
  methods: {
    ...mapActions('d2admin/menu', [
      'asideCollapseToggle'
    ]),
    /**
     * 接收点击切换侧边栏的按钮
     */
    handleToggleAside () {
      this.asideCollapseToggle()
    }
  }
}
</script>

<style lang="scss">
// 注册主题
@import '~@/assets/style/theme/register.scss';
</style>

<vue-filename-injector>
export default function (Component) {
  Component.options.__source = "src/layout/header-aside/layout.vue"
}
</vue-filename-injector>
